import { useEffect } from 'react'
import Header from '../../assets/now-header.png'
import BodyImg from '../../assets/img2.png'
import AOS from 'aos'
import 'aos/dist/aos.css'
import Sidebar from '../sidebar/sidebar'
import Posts from '../posts/posts'

import './now.css'

type Props = {
  children?: any
}

export default function Now(props: Props) {
  useEffect(() => {
    AOS.init({})
  }, [])

  return (
    <div className='header'>
      <img className='headerImg' src={Header} alt='' />
      <div className='body'>
        <div className='bodyP'>
          Dennis Christensen and many other Jehovah's Witnesses in Russia <br />
          are being jailed, detained, and persecuted, men and women, for their
          religion.
        </div>
        <img
          data-aos='fade-right'
          data-aos-duration='1300'
          className='bodyI'
          src={BodyImg}
          alt=''
        />
      </div>
    </div>
  )
}
