import React, { useState } from 'react'
import * as S from './styles'
import Now from '../pages/now'
import Logo from '../../assets/logo.png'

import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  Redirect,
} from 'react-router-dom'

type Props = {
  open: boolean
}

function RightNav(props: Props) {
  return (
    <Router>
      <S.Ul open={props.open}>
        <NavLink
          to='/home'
          activeStyle={{
            color: '#ffc106',
          }}
        >
          <li>HOME</li>
        </NavLink>
        <NavLink
          to='/history'
          activeStyle={{
            color: '#ffc106',
          }}
        >
          <li>HISTORY</li>
        </NavLink>
        <NavLink
          to='/now'
          activeStyle={{
            color: '#ffc106',
          }}
        >
          <li>HAPPENING NOW</li>
        </NavLink>
        <NavLink
          to='/genocide'
          activeStyle={{
            color: '#ffc106',
          }}
        >
          <li>8 STAGES OF GENOCIDE</li>
        </NavLink>
        <NavLink
          to='/story'
          activeStyle={{
            color: '#ffc106',
          }}
        >
          <li>STORY</li>
        </NavLink>
      </S.Ul>

      <Switch>
        <Route exact path='/home'></Route>
        <Route exact path='/history'></Route>
        <Route path='/now' component={Now}></Route>
        <Route exact path='/genocide'></Route>
        <Route exact path='/story'></Route>
        <Redirect to='/home' />
      </Switch>
    </Router>
  )
}

export default RightNav
